/*evento animacion input*/
:focus{outline: none;}

.input-effect{
    float: left;
    width: 29%;
    margin-top: 100px;
    margin-left: 31%;
    position: relative;
    
  }

.effect-16{
    border: 0px; 
    padding: 1% 4%;
    border-bottom: 3px solid rgb(43, 44, 80);
    background-color: rgba(237, 236, 235, 0.7);
    color: #333;
    width: 100%; 
    box-sizing: border-box;
    letter-spacing: 1px;
    font-size: 1.3rem;
  }

.effect-16 ~ .focus-border{
    position: absolute;
    bottom: 0;
    right: 0;
    width: 0;
    height: 4px;
    background-color: rgb(26, 205, 211);
    transition: 0.6s;
   }

.effect-16:focus ~ .focus-border,.has-content.effect-16 ~ .focus-border{
        width: 100%;
        transition: 0.6s;
  }

